<template>
  <div>
    <div class="nav">
      <el-button type="text" icon="el-icon-arrow-left" class="back" @click="$router.back()">
        {{$t('order-meeting.statistics.undo')}}
      </el-button>
    </div>
    <row class="total flex-space-between" flex="1">
      <column>
        <span class="num">{{OrderDetail.totalNumber}}
          <span style="font-size:14px">{{$t('order-meeting.statistics.units')}}</span>
        </span>
        <span style="color: #666">
          {{submitType?
          $t('order-meeting.statistics.intention.styles_with_submitted_orders')
          :$t('order-meeting.statistics.intention.styles_with_draft_orders')}}
        </span>
      </column>
      <row class="order-info flex-vertical-center">
        <column>
          <span>{{
            $t('order-meeting.statistics.intention.salesperson')
          }}：{{OrderDetail.salesManagerName}}</span>
          <span v-if="submitType">
            {{$t('order-meeting.statistics.intention.order_number')}}：{{OrderDetail.orderId}}
          </span>
        </column>
        <div style="width:1px;height:27px;border-right: 1px solid #03234C;"></div>
        <column>
          <span>{{
            $t('order-meeting.statistics.intention.contact_people')
          }}：{{OrderDetail.buyerName}}</span>
          <span v-if="submitType">{{
            $t('order-meeting.statistics.intention.telephone')
          }}：{{OrderDetail.phone}}</span>
        </column>
        <column v-if="submitType">
          <span>{{
            $t('order-meeting.statistics.intention.company')
          }}：{{OrderDetail.companyName}}</span>
          <span>{{
            $t('order-meeting.statistics.intention.shipping_address')
          }}：{{OrderDetail.receiverAddress}}</span>
        </column>
      </row>
    </row>
    <div class="table">
      <el-table :data="OrderDetail.shoeList">
        <el-table-column
          prop="curtailImg"
          width="160"
          :label="$t('order-meeting.statistics.intention.style')">
          <template slot-scope="scope">
            <BaseImage :src="scope.row.curtailImg"
              style="height:90px; width:160px"
              fit="contain"></BaseImage>
          </template>
        </el-table-column>
        <el-table-column
          prop="shoeName">
        </el-table-column>
        <el-table-column
          prop="colorName"
          :label="$t('order-meeting.statistics.color')">
        </el-table-column>
        <el-table-column
          prop="collection"
          :label="$t('order-meeting.statistics.intention.collection')">
        </el-table-column>
        <el-table-column
          prop="shoeNumber"
          :label="$t('order-meeting.statistics.intention.quantity')">
          <template slot-scope="scope">
            {{ scope.row.shoeNumber}}{{$t('order-meeting.statistics.units')}}
          </template>
        </el-table-column>
        <el-table-column
          prop="gmtCreateTime"
          :label="$t('order-meeting.statistics.intention.time')">
          <template slot-scope="scope">
            {{ scope.row.gmtCreateTime | date('YYYY/MM/DD HH:mm') }}
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { getOrderDetails, getUndeliveredOrderDetails } from '@/api/order-meeting';
import BaseImage from '@/components/base/Image';

export default Vue.extend({
  name: 'ConfirmIntention',
  components: {
    BaseImage,
  },
  props: {
    orderId: {
      default() {
        return this.$route.query.orderId;
      },
    },
    userId: {
      default() {
        return this.$route.query.userId;
      },
    },
    submitType: {
      default() {
        if (typeof this.$route.query.submitType === 'boolean') {
          return this.$route.query.submitType;
        }
        return this.$route.query.submitType !== 'false';
      },
    },
    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
  },
  data() {
    return {
      OrderDetail: {},
    };
  },
  created() {
    this.getOrderDetails();
  },
  methods: {
    async getOrderDetails() {
      if (this.submitType) {
        this.OrderDetail = (await getOrderDetails(this.orderId, this.workspaceId)).data;
      } else {
        this.OrderDetail = (await getUndeliveredOrderDetails(this.userId, this.workspaceId)).data;
      }
    },
  },
});
</script>

